import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";

export default function studentForm() {
    const [studentFirst, setFirst] = useState('');
    const [studentLast, setLast] = useState('');
    const [Stu_id, setId] = useState('');
    const [Course_id, setCourseId] = useState('');

    const history = createBrowserHistory();

    const directToSuccess = () => {
        history.push('/studentsuccess');
        window.location.reload();
    };

    const handleSubmit = (e) => {
        const obj = { studentFirst, studentLast, Stu_id, Course_id };

        axios
            .post(
                'https://sheet.best/api/sheets/e57f2e8a-dff6-4a23-9145-c81449e9fd2e',
                obj
            )
            .then((response) => {
                console.log(response);
            });
        
            directToSuccess();
    };

    return(
        <Container fluid className="container">
            <Header as="h3">Please fill out the following information for the new student.</Header>
            <div></div><br/>

            <Form className="form">
                <Form.Field>
                    <p>Student First Name</p>
                    <textarea id="studentFirst" name="studentFirst" rows="1" cols="50" onChange={(e) => setFirst(e.target.value)}></textarea>
                </Form.Field>
                <div></div><br />

                <Form.Field>
                    <p>Student Last Nme</p>
                    <textarea id="studentLast" name="studentLast" rows="1" cols="50" onChange={(e) => setLast(e.target.value)}></textarea>
                </Form.Field>
                <div></div><br />

                <Form.Field>
                    <p>Student ID</p>
                    <textarea id="Stu_id" name="Stu_id" rows="1" cols="25" onChange={(e) => setId(e.target.value)}></textarea>
                </Form.Field>
                <div></div><br />

                <Form.Field>
                    <p>Course ID</p>
                    <select name="Course_id" id="Course_id" onChange={(e) => setCourseId(e.target.value)}>
                        <option value="default" selected="true">Choose a Course ID</option>
                        <option>104</option>
                        <option>102</option>
                        <option>106</option>
                        <option>108</option>
                        <option>232</option>
                        <option>222</option>
                        <option>233</option>
                        <option>205</option>
                        <option>218</option>
                        <option>237</option>
                        <option>227</option>
                        <option>223</option>
                    </select>
                </Form.Field>
                <div></div><br />

                <Button color="black" type='submit' onClick={handleSubmit}>Add Student</Button>
                <Button variant="outline-light" size="lg"><Link to="/professoraddstudent">Back</Link></Button>
            </Form>
        </Container>
    );
}